var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('article',{staticClass:"xianhe-house"},[_c('section',{staticClass:"xianhe-house__ch xianhe-house__ch--1"},[_c('h1',{staticClass:"xianhe-house__ch-title"},[_vm._v("美食 讓人從副業變專業")]),_c('section',{staticClass:"xianhe-house__ch-content"},[_c('p',[_vm._v("\n        鮮禾屋的老闆鄭清任先生曾經是銀行員，太太蔡芷涵是經營補習班的老師，兩人都是道地的水林人，平常都熱愛美食。鄭老闆表示：「剛開始是我小舅子在北部餐廳當主廚，待過很多餐廳，目前在北港經營餐館，他擅長廣式料理，很會做餐前小菜。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/xianhe_house/鮮禾屋的老闆與老闆娘.jpg"),"alt":"鮮禾屋的老闆與老闆娘"}}),_c('p',[_vm._v("\n        2009年他剛從北部回來，因為這樣機緣我和太太就跟他一起學料理。」當時正值鄭老闆退休之際，便開始全力投身於食品的研發，「原本純粹是玩票性質，隨時喊停就停，沒想到這一做就做了十年，不自覺地做出信心和興趣了。」鄭老闆幽默回應。\n      ")])])]),_c('section',{staticClass:"xianhe-house__ch xianhe-house__ch--2"},[_c('h1',{staticClass:"xianhe-house__ch-title"},[_vm._v("黃金泡菜 是一道料理")]),_c('section',{staticClass:"xianhe-house__ch-content"},[_c('p',[_vm._v("\n        鮮禾屋在一年後2010年正式成立，鮮禾屋的「鮮」字義為「新鮮」和「當令」之意，「禾」象徵農作物。\n      ")]),_c('p',[_vm._v("\n        「最開始研發出來的是黃金泡菜，也是我們的主打商品。在經歷一年多次的試做失敗後，才做出自己認為最好又最完美的口味，泡菜的研發過程，味道的調配和作法都是技巧，從白菜的挑選開始到新鮮蔬菜打成醬泥，不管是味道、香氣、口感和鮮度都要符合我的標準，要做就做到最好！」鄭老闆說。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/xianhe_house/醃製泡菜過程.jpg"),"alt":"醃製泡菜過程"}}),_c('p',[_vm._v("\n        我們在試吃時，覺得白菜很有水分，其葉菜很鮮嫩且口感很鮮脆，醬泥很有層次且口味溫潤，完全沒有刺味和生菜的腥味，可以吃到老闆的用心。\n      ")])])]),_c('section',{staticClass:"xianhe-house__ch xianhe-house__ch--3"},[_c('h1',{staticClass:"xianhe-house__ch-title"},[_vm._v("食材 「當令」的尚好")]),_c('section',{staticClass:"xianhe-house__ch-content"},[_c('p',[_vm._v("\n        「我們選用的白菜是韓國來的，因為夏天八月和九月的白菜品質並不好，我也不會用冷凍的食材，去增加店裡的產量。所以後來就開始研發「梅香白玉」，白玉是菜頭，夏天是產季且材質是鮮脆而細嫩。「珍饌蕃茄」的番茄是水林小農所提供的食材，番茄完全用手工剝皮，希望呈現出入口即化的口感。番茄產季是十月到四月，可以補足店裡的貨源。今年十月左右鮮禾屋的新產品「洛神蜜餞」就要開賣了！」鄭老闆表示。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/xianhe_house/店內販售的產品.jpg"),"alt":"店內販售的產品"}}),_c('p',[_vm._v("\n        除了這些產品之外，鮮禾屋也有養生黑棗、珍饌蕃茄、洛神双梅湯、牛蒡醋等商品。\n      ")])])]),_c('section',{staticClass:"xianhe-house__ch xianhe-house__ch--4"},[_c('h1',{staticClass:"xianhe-house__ch-title"},[_vm._v("好客 把老家妝點成「客廳」")]),_c('section',{staticClass:"xianhe-house__ch-content"},[_c('p',[_vm._v("\n        鮮禾屋位於水林市區的外圍，是鄭老闆的老家。老家前面原本的車庫，在2013年為因應雲林農業博覽會，設置會議桌讓訪客方便進來休息。之後老闆自己當起木工，買木材、拋光、上油等，讓車庫改換成有百香果棚架的庭院，隨後並將剩餘的木材製成店內的桌椅，營造出木質天然又溫暖的空間。\n      ")]),_c('p',[_vm._v("\n        2016年為了讓店裡更有特色，邀請地方著名石頭屋設計師紀連東先生，用石材堆砌，讓店內增添一些趣味，呈現出內外部居家庭院咖啡屋的整體空間。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/xianhe_house/鮮禾屋外部DIY庭院.jpg"),"alt":"鮮禾屋外部DIY庭院"}}),_c('p',[_vm._v("\n        鮮禾屋沒有菜單，只有提供免費的咖啡和自家飲品，作為消費者、產品和製造過程分享的一種交流方式。\n      ")])])]),_c('section',{staticClass:"xianhe-house__ch xianhe-house__ch--5"},[_c('h1',{staticClass:"xianhe-house__ch-title"},[_vm._v("食品 需要用良心交朋友")]),_c('section',{staticClass:"xianhe-house__ch-content"},[_c('p',[_vm._v("\n        為了精益求精老闆娘再度回到校園，在虎尾科技大進修食品加工碩士班課程，探究食物營養的正確攝取等相關知識。\n      ")]),_c('p',[_vm._v("\n        鄭老闆表示：「雖然鮮禾屋成立時純粹是因為興趣，但我們非常注重安全和衛生，所以食材的「洗」、「燙」是自我把關的重點。我們用交朋友的方式和大家分享美食的喜悅，這裡沒有農藥、化學添加劑、防腐劑，我們認為「好物」的信念就是一種社會責任和道德認知的良心體現。」在採訪的同時，我們體會到鮮禾屋的產品就和他們本人一樣，真誠、實在又溫暖！\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/xianhe_house/鮮禾屋內部吧檯.jpg"),"alt":"鮮禾屋內部吧檯"}})])])])}
